import React from 'react';
import '../css/Home.css';
import Nav from '../Components/Nav';
import Carousel from './Carousel';
import ContentData from './ContentData';
import Footer from './Footer';

class Home extends React.Component{
    render(){
        return(
            <div className="Home">
                <Nav />
                <Carousel />
                <ContentData />
                <Footer />
            </div>
        );
    }
}

export default Home;