import React from 'react';
import Axios from 'axios';

class Dashboard extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            authStatus : 0,
        }
    }

    authValid(){
        Axios.get(`http://localhost:9000/getSession`, {})
            .then(res => {
                const data = res.data
                console.log(data)

            })
            .catch((error) => {
                console.log(error)
                if (error.response) {
                    console.log(error.response.data);
                    console.log(error.response.status);
                    console.log(error.response.headers);
                }
            })
    }

    componentDidMount(){
        this.authValid();
    }
    render(){
        return(
            <div className="container-fluid">
                <center><h1>Welcome..!</h1></center>
                <div><a href="http://localhost:9000/logout">Logout</a></div>
            </div>
        );
    }
}

export default Dashboard;