import React from 'react';
import '../css/ContentData.css';
import jQuery from 'jquery'; 

class ContentData extends React.Component{
    render(){
        var scroll = window.requestAnimationFrame || 
            function(callback) {
                window.setTimeout(callback,1000/60)
            };
        var elementsToShow = document.querySelectorAll('.ContentData1');
        function loop() {
            elementsToShow.forEach((element) => {
                if(isElementInViewport(element)){
                    element.classList.add('is-visible');
                }else{
                    element.classList.remove('is-visible');
                }
            });
            scroll(loop);
        }
        loop();
        function isElementInViewport (el) {

            //special bonus for those using jQuery
            if (typeof jQuery === "function" && el instanceof jQuery) {
                el = el[0];
            }
        
            var rect = el.getBoundingClientRect();
        
            return (
                rect.top >= 0 &&
                rect.left >= 0 &&
                rect.bottom <= (window.innerHeight || document.documentElement.clientHeight) && /*or $(window).height() */
                rect.right <= (window.innerWidth || document.documentElement.clientWidth) /*or $(window).width() */
            );
        }
        return(
            <div className="ContentData">
                <div className="OneContentData">
                    <br />
                    <h1>Our Services</h1>
                    <div className="ContentData1" >
                        
                        <center><h1>Nature Photography</h1></center>
                    </div>
                </div>
                <div className="TwoContentData">    
                    <br />
                    <div className="ContentData1" >
                        <center><h1>Wedding Photography</h1></center>
                    </div>
                </div>
                <div className="ThreeContentData">    
                    <br />
                    <div className="ContentData1" >
                        <center><h1>Action/Sports Photography</h1></center>
                    </div>
                </div>
                <div className="FourContentData">    
                    <br />
                    <div className="ContentData1" >
                        <center><h1>Baby Photography</h1></center>
                    </div>
                </div>
            </div>
        );
    }
}

export default ContentData;