import React from "react";
import img1 from "../static/01/006.jpg";
import img2 from "../static/01/003.jpg";
import img3 from "../static/01/004.jpg";
import '../css/Carousel.css';

class Carousel extends React.Component {
  render() {
    const textCarousel = {
      color:'white',
    }
    return (
      <div>
        <div id="demo" class="carousel slide" data-ride="carousel">
          <ul class="carousel-indicators">
            <li data-target="#demo" data-slide-to="0" class="active"></li>
            <li data-target="#demo" data-slide-to="1"></li>
            <li data-target="#demo" data-slide-to="2"></li>
          </ul>

          <div class="carousel-inner">
            <div class="carousel-item active">
              <img src={img1} alt="FKP" />
                <div class="absolute-div">
                  <div class="carousel-caption">
                    <h3 style={textCarousel}>“What i like about photographs is that they<br /> capture a moment that’s gone forever, impossible to reproduce.” </h3>
                  </div>
                </div>
            </div>
            <div class="carousel-item">
              <img src={img2} alt="FKP" />
                <div class="absolute-div">
                  <div class="carousel-caption">
                    <h3>“A picture is a secret about a secret, the more it tells you the less you know.” </h3>
                  </div>
                </div>
            </div>
            <div class="carousel-item">
              <img src={img3} alt="FKP" />
                <div class="absolute-div">
                  <div class="carousel-caption">
                    <h3>“You don't take a photograph, you make it.” </h3>
                  </div>
                </div>
            </div>
          </div>
          <a class="carousel-control-prev" href="#demo" data-slide="prev">
            <span class="carousel-control-prev-icon"></span>
          </a>
          <a class="carousel-control-next" href="#demo" data-slide="next">
            <span class="carousel-control-next-icon"></span>
          </a>
        </div>
      </div>
    );
  }
}
export default Carousel;
