import React from 'react';
import axios from 'axios';
import  { Redirect } from 'react-router-dom'

class Admin extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            username : "",
            password : "",
            formStatus : false,
            authStatus : false,
            login: false,
        }
    }

    
    changeHandler = (e) =>{
        this.setState({[e.target.name] : e.target.value});
    }
    submitHandler= (e) => {
        e.preventDefault();
        this.setState({
            formStatus : false,
            authStatus : false,
        })
        axios.post("http://localhost:9000/admin",this.state)
            .then(response =>{
                var status = response.status;
                if(status !== 200){
                    this.setState({formStatus : true})
                }else{var length = response.data.length;
                    if(length === 1){
                        this.setState({
                            authStatus : true,
                            login: true,
                        })
                        
                    }else{
                        this.setState({formStatus : true})
                    }
                }
                
                setInterval(()=> {
                    this.setState({
                        formStatus : false,
                        authStatus : false,
                    })
                },10000)
            })
    }   
    render(){
        const {username ,password, formStatus, authStatus, login} = this.state;
        if(login){
            return <Redirect to='/dashboard'  />
        }
        return (
            <div>
                <div className="admin container">
                    <h2>Admin | FKP</h2>
                    {formStatus ? <div className="alert alert-danger">Invalid details</div> : null}
                    {authStatus ? <div className="alert alert-success">Logged in successfully</div> : null}
                    <form onSubmit={this.submitHandler}>
                        <input name="username" className="form-control" type="text" onChange={this.changeHandler} value={username} placeholder="Enter Username" required  /><br />
                        <input name="password" className="form-control" type="password" onChange={this.changeHandler} value={password} placeholder="Enter Password" required  /><br />
                        <input type="submit" value="Login" className="btn btn-success" />
                    </form>
                </div>
            </div>
        );
    }
}

export default Admin;